import React, { useEffect, useState } from "react";
import {
    Blockie,
    Box,
    Button,
    EthAddress,
    Field,
    Form,
    Heading,
    Input,
    Text,
    ToastMessage,
} from "rimble-ui";
import ChatHistoryItem from "./ChatHistoryItem";

function ChatHistory({ set }) {
    const randomColor = () =>
        `#${Math.floor(Math.random() * 16777215).toString(16)}`;
    const [toggle, setToggle] = useState(false);
    const [friend, setFriend] = useState("");
    useEffect(() => {
        window.peer.identity.subscribe((e) => {
            try {
                localStorage.setItem(JSON.parse(e).from, e);
                set(JSON.parse(e).from);
            } catch {}
        });
    }, []);

    function newFriend(event) {
        event.preventDefault();
        try {
            if (!localStorage.getItem(friend))
                window.peer.privateMSG(undefined, friend, "WHO");
            else {
                set(friend);
            }
        } catch {
            window.toastProvider.addMessage(
                "Couldn't connect to your friend ðŸ˜Œ",
                {
                    secondaryMessage:
                        "May be you entered a wrong address or they are offline",
                    colorTheme: "light",
                    variant: "failure",
                }
            );
        }
        setFriend("");
    }
    function hideLogs() {
        setToggle(!toggle);
        if (toggle) {
            document.querySelector(".logs").style = "";
            document.querySelector(".chats").style = "";
            document.querySelector(".chats").children[0].style = "";
            document.querySelector(".chats").children[1].style = "";
        } else {
            document.querySelector(".logs").style = "display: none";
            document.querySelector(".chats").style = "width: 100%";
            document.querySelector(".chats").children[0].style = "width: 35%";
            document.querySelector(".chats").children[1].style = "width: 65%";
        }
    }
    return (
        <div>
            <Box
                bg={"#2f3136"}
                p={2}
                style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                }}
            >
                <Blockie
                    style={{ borderRadius: "50%" }}
                    opts={{
                        seed: sessionStorage["whoami"],
                        color: randomColor(),
                        bgcolor: randomColor(),
                        spotcolor: randomColor(),
                    }}
                />

                <Heading.h6 style={{ margin: 0 }}>
                    Chats of {window.user.userInfo.username || "Anonymous"}
                </Heading.h6>
                <span style={{ display: "flex", alignItems: "center" }}>
                    <Button.Text
                        icon="Info"
                        icononly={true}
                        size="small"
                        title="Stats for Nerds"
                        mr={2}
                        className="logsBTN"
                        onClick={hideLogs}
                        style={{ color: "#ffffff" }}
                    />
                    <Button
                        icon="PowerSettingsNew"
                        size="small"
                        iconpos="right"
                        onClick={() => window.location.reload()}
                    >
                        Log Out
                    </Button>
                </span>
            </Box>
            <Box p={2} bg={"#36393f"}>
                <Text fontSize="1">Your Meschain Address</Text>
                <EthAddress
                    address={sessionStorage["whoami"]}
                    className="hideQR"
                />
            </Box>
            <Box p={2} bg={"#2f3136"}>
                <Form className="fullWidth">
                    <Field label="Your Friends Meschain Address">
                        <Input
                            type="text"
                            placeholder="Ask your friend to share their address"
                            style={{ width: "100%" }}
                            required={true}
                            value={friend}
                            onChange={(e) => setFriend(e.target.value)}
                        />
                    </Field>
                    <Button
                        icon="Add"
                        icononly={true}
                        ml={2}
                        onClick={newFriend}
                    ></Button>
                </Form>
            </Box>
            <Box bg={"#46484b"} style={{ height: "100%", overflowY: "auto" }}>
                <ChatHistoryItem
                    name="Anurag Verma"
                    lastText="Hi, what's up?"
                    address="0xa8f3bc6f5cdd3bfa00a538d11bed3ad3ea8c47005df4e0e05b8319d818ece8bf"
                    handler={set}
                />
                <ChatHistoryItem
                    name="Antra Verma"
                    lastText="Good Night! ðŸ˜€"
                    address="0xa984b008e292ef2c2993635f4fbb794b6f3366bc30c845f4581308c31b63fa4f"
                    handler={set}
                />
            </Box>
            <>
                <ToastMessage.Provider
                    ref={(node) => (window.toastProvider = node)}
                />
            </>
        </div>
    );
}

export default ChatHistory;
