import React, { useState, useEffect, useRef } from "react";
import { Box, Text, Heading, Blockie, Input, Button } from "rimble-ui";
import NoMsg from "../../assets/nochat.png";
import Message from "./Message";
import UserInfo from "./UserInfo";
function ChatPage({ curr }) {
    const [conn, setConn] = useState(0);
    const randomColor = () =>
        `#${Math.floor(Math.random() * 16777215).toString(16)}`;
    useEffect(() => {
        window.peer.numConnections.subscribe((e) => setConn(e));
    }, [conn]);
    return (
        <div className="hideChat">
            <Box style={{ height: "100%" }} color="#ffffff">
                {!curr && (
                    <React.Fragment>
                        <div
                            style={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                height: "inherit",
                            }}
                        >
                            <span
                                style={{
                                    display: "flex",
                                    alignItems: "center",
                                    justifyContent: "center",
                                    flexDirection: "column",
                                    gap: "0.5rem",
                                }}
                            >
                                <img src={NoMsg} width="200" />
                                <Heading.h4 my={1}>
                                    It's always nice to chat with to someone
                                </Heading.h4>
                                <Text my={0}>
                                    Pick a chat from the left menu, or add your
                                    friends to start a new chat.
                                </Text>
                                <Text my={1}>
                                    By the way you're currently directly
                                    connected to <b>{conn}</b> users.
                                </Text>
                                <small
                                    style={{
                                        marginTop: "2rem",
                                        width: "60%",
                                    }}
                                >
                                    All your messages are always end-to-end
                                    encrypted to never go to any server.
                                    Everything happens on a blockchain powered
                                    by people like you. <wbr /> Meschain is
                                    built on top of blockchain where all the
                                    users compete to add your new message to the
                                    blockchain. Note that once you've sent a
                                    message it is for forever no one (not even
                                    you) can alter or delete it.
                                </small>
                            </span>
                        </div>
                    </React.Fragment>
                )}
                {curr && (
                    <React.Fragment>
                        <Box
                            bg={"#4a4a4a"}
                            p={2}
                            style={{
                                display: "flex",
                                alignItems: "center",
                            }}
                        >
                            <UserInfo addr={curr} />
                        </Box>
                        <div>
                            <div class="card-body msg_card_body">
                                <div
                                    style={{
                                        display: "flex",
                                        "justify-content": "center",
                                        transform: "scale(0.8)",
                                    }}
                                >
                                    <Button
                                        size="small"
                                        mr={3}
                                        style={{
                                            "background-color": "a7a7ad",
                                        }}
                                    >
                                        Today
                                    </Button>
                                </div>
                                <Message
                                    message="Hellow!"
                                    time="10:20AM"
                                    sent={true}
                                />
                                <Message
                                    message="Hellow Lisfgoaif skdhfoisdg sghio!"
                                    time="10:20AM"
                                    sent={false}
                                />
                                <Message
                                    message="Hellow!"
                                    time="10:20AM"
                                    sent={true}
                                />
                                <Message
                                    message="Hellow!"
                                    time="10:20AM"
                                    sent={true}
                                />
                            </div>
                            <div
                                style={{
                                    display: "flex",
                                    alignItems: "center",
                                }}
                            >
                                <Button.Outline
                                    icon="Attachment"
                                    icononly
                                    style={{
                                        "border-bottom-right-radius": 0,
                                        "border-top-right-radius": 0,
                                        "background-color": "#ffffff",
                                        "margin-right": 0,
                                        "margin-left": 25,
                                    }}
                                ></Button.Outline>
                                <Input
                                    type="text"
                                    required={true}
                                    placeholder="Type your message..."
                                    style={{
                                        width: "60vw",
                                        margin: "5px 0px",
                                        "border-radius": 0,
                                    }}
                                />
                                <Button
                                    icon="Send"
                                    icononly
                                    style={{
                                        "margin-right": "30px",
                                        "border-top-left-radius": 0,
                                        "border-bottom-left-radius": 0,
                                    }}
                                />
                            </div>
                        </div>
                    </React.Fragment>
                )}
            </Box>
        </div>
    );
}

export default ChatPage;
