import React, { useEffect, useState } from "react";
import { Avatar, Text, Blockie } from "rimble-ui";
function UserInfo({ addr }) {
    const randomColor = () =>
        `#${Math.floor(Math.random() * 16777215).toString(16)}`;
    const [name, setName] = useState("Anonymous Panda");

    useEffect(() => {
        console.log("Update");
        setTimeout(() => {
            // Returns NULL sometimes fix this...
            localStorage.getItem(addr.address);
        }, 2500);
    }, [addr.address]);

    return (
        <React.Fragment>
            {addr.blockie && <Avatar src={addr.blockie} size="45px" />}
            {!addr.blockie && (
                <Blockie
                    style={{ borderRadius: "50%" }}
                    opts={{
                        seed: addr.address,
                        color: randomColor(),
                        bgcolor: randomColor(),
                        spotcolor: randomColor(),
                    }}
                />
            )}
            <Text p={2}>{name || "Anonymous Panda"}</Text>
        </React.Fragment>
    );
}

export default UserInfo;
